process.env.NODE_ENV = 'test';
import chai from 'chai';
import chaiHttp from 'chai-http';
import server from '../src/app';
import Admin from '../src/models/admin';
const { assert, expect } = chai;
chai.use(chaiHttp);

const PREFIX = '/api/v1';

let login_details = {
  email: `admin@gmail.com`,
  password: `123@abc`
};

let register_details = {
  name: `Admin`,
  email: `admin@gmail.com`,
  phone: `07060538862`,
  password: `123@abc`
};

beforeEach(done => {
  done();
});

describe('Admin Auth Signup Tests', () => {
  it('POST /auth/admin/signup - Admin Can Sign Up', done => {
    chai
      .request(server)
      .post(`${PREFIX}/auth/admin/signup`)
      .send({
        register_details
      })
      .then(res => {
        expect(res).to.have.status(201);
        expect(res.body).to.have.status('success');
        expect(res.body).to.be.a('object');
        expect(res).to.be.json;
        done();
      })
      .catch(err => console.log('POST /auth/admin/signup', err.message));
  });

  it("POST /auth/admin/signup - Admin Can't signup again with the same email", done => {
    chai
      .request(server)
      .post(`${PREFIX}/auth/admin/signup`)
      .send({
        register_details
      })
      .then(res => {
        expect(res).to.have.status(500);
        assert.equal(res.body).status('error');
        done();
      })
      .catch(err => console.log('POST /auth/admin/signup', err.message));
  });
});

describe('Admin Can Login Endpoint Tests', () => {
  it('POST /auth/admin/login - Admin Cannot Login without first registered', done => {
    chai
      .request(server)
      .post(`${PREFIX}/auth/admin/login`)
      .send({
        login_details
      })
      .then(res => {
        expect(res).to.have.status(500);
        assert.equal(res.body.status, 'error');
        done();
      })
      .catch(err => console.log('POST /auth/admin/login', err.message));
  });
  it('POST /auth/admin/login - Admin Can Login', done => {
    chai
      .request(server)
      .post(`${PREFIX}/auth/admin/login`)
      .send({
        login_details
      })
      .then(res => {
        expect(res).to.have.status(200);
        assert.equal(res.body.status, 'success');
        done();
      })
      .catch(err => console.log('POST /auth/admin/login', err.message));
  });
  it("POST /auth/admin/login - Admin Can't login with invalid password", done => {
    chai
      .request(server)
      .post(`${PREFIX}/auth/admin/login`)
      .send({
        email: 'admin@gmail.com',
        password: 'password111'
      })
      .then(res => {
        expect(res).to.have.status(500);
        assert.equal(res.body.status, 'error');
        done();
      })
      .catch(err => console.log('POST /auth/admin/login', err.message));
  });
});

after(done => {
  Admin.destroy({
    where: {
      email: 'admin@gmail.com'
    }
  }).then(() => {
    done();
  });
});
